.main-nav__toogle {
  position: absolute;
  right: 22px;
  top: -49px;
  height: 18px;
  width: 18px;
  font-size: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.main-nav__toogle:hover, .main-nav__toogle:focus {
  opacity: 0.6;
}

.main-nav__toogle:active {
  opacity: 0.3;
}

@media (min-width: 768px) {
  .main-nav__toogle {
    display: none;
  }
}

.main-nav--closed .main-nav__toogle::before {
  content: "";
  position: absolute;
  left: 8px;
  top: -3px;
  width: 2px;
  height: 25px;
  background-color: #000000;
  transform: rotate(-90deg);
  box-shadow: 8px 0 0 0 #000000, -8px 0 0 0 #000000;
}

.main-nav--opened .main-nav__toogle::after {
  content: "";
  position: absolute;
  left: 8px;
  top: -3px;
  width: 2px;
  height: 25px;
  background-color: #000000;
  transform: rotate(44deg);
}

.main-nav--opened .main-nav__toogle::before {
  content: "";
  position: absolute;
  left: 8px;
  top: -3px;
  width: 2px;
  height: 25px;
  background-color: #000000;
  transform: rotate(-44deg);
}

@media (max-width: 767px) {
  .main-nav--nojs .main-nav__toogle {
    display: none;
  }
  .main-nav--closed .site-list {
    display: none;
