.programs__link::before {
  content: "";
  display: inline-block;
  position: absolute;
  left: 100px;
  bottom: 11px;
  width: 20px;
  height: 2px;
  background-color: #000000;
  transition: width 0.2s ease;
}

@media (min-width: 768px) {
  .programs__link::before {
    left: 133px;
    bottom: 14px;
  }
}

.programs__link::after {
  content: "";
  display: inline-block;
  position: absolute;
  left: 118px;
  bottom: 6px;
  width: 0;
  height: 0;
  border-top: 6px solid transparent;
  border-bottom: 6px solid transparent;
  border-left: 7px solid #000000;
  transition: left 0.2s ease;
}

@media (min-width: 768px) {
  .programs__link::after {
    left: 150px;
    bottom: 9px;
  }
}

.programs__link::after:hover::before {
  width: 25px;
  transition: width 0.2s ease;
}

.programs__link::after:hover::after {
  left: 125px;
  transition: left 0.2s ease;
}

.programs__link::after:active, .programs__link::after:active::before, .programs__link::after:active::after {
  opacity: 0.3;
}

.programs__link::after:active {
